import React from 'react'
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom'
import Home from './Home'
import Dingdan from './Dingdan'
import My from './My'
import Tab from './Tab'
import Shoppingcar from './Shoppingcar'
import More from './More'
import Search from './Search'
import Detail from './Detail'

// 1、搜索框（点击跳转搜索页面，顶部是searchBar，点取消，返回首页）
// 2、轮播图
// 3、商品列表（10个）（点击跳转商品详情页，
// 详情页写上面的图片和返回箭头，点返回箭头返回首页
// 和底部按钮（店铺、客服、收藏、加入购物车和立即购买）
const App = () => {
	return <Router>
		<Switch>
			<Route exact path='/' component={Home}></Route>
			{/* <Route path='/home/detail' component={Dingdan}></Route> */}
			<Route path='/shoppingcar' component={Shoppingcar}></Route>
			<Route path='/dingdan' component={Dingdan}></Route>
			<Route path='/my' component={My}></Route>
			<Route path='/more' component={More}></Route>
			<Route path='/search' component={Search}></Route>
			<Route path='/detail' component={Detail}></Route>
			<Route render={() => <Redirect to='/' />}></Route>
		</Switch>
		{/* <Tab /> */}
	</Router>
}

export default App;

