import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile'

class Lb extends React.Component {
	state = {
		data: ['1', '2', '3','4','5'],
		imgHeight: 300,
	}
	componentDidMount() {setTimeout(() => {
			this.setState({
				data: [1, 2, 3,4,5]
			});
		}, 100);
	}
	render() {
		return (
			<WingBlank>
				<Carousel
					autoplay={true}
					infinite
				>
					<a
						// key={val}
						href="http://www.alipay.com"
						style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
					>
						<img
							src={'https://gw.alicdn.com/imgextra/i2/16/O1CN01kfLCdP1BzMy0efa9R_!!16-0-lubanu.jpg'}
							alt=""
							style={{ width: '100%', verticalAlign: 'top' }}
							onLoad={() => {
								window.dispatchEvent(new Event('resize'));
								this.setState({ imgHeight: 'auto' });
							}}
						/>
					
					</a>
					<img src='https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg'/>
					<img src='https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg'/>
					<img src='https://gw.alicdn.com/imgextra/i4/176/O1CN01CyzbGQ1DAeL2sHz86_!!176-0-lubanu.jpg'/>
					<img src='https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000005168/O1CN01RQ8EaH1o2zV1vITsc_!!6000000005168-0-octopus.jpg'/>
				</Carousel>
			</WingBlank>
		);
	}
}

export default Lb;