import React from 'react'
import {connect} from 'react-redux'
const Todos = (props) => {
    console.log(props);

    return (
        <div>
            {
                props.mydata.map((item,idx)=>(
                    <li key={idx}>
                        {item}
                        <button
                            onClick={()=>{
                                props.dispatch({
                                    type: "DEL_TODO",
                                    index: idx
                                })
                            }}
                        >删除</button>
                    </li>
                ))
            }
            {/* <button 
                onClick={()=>props.dispatch({
                    type:"add_todo",
                    value:'100000'
                })}
            >添加</button> */}
        </div>
    )
}

const mapStateToProps = (state)=>{
    console.log(state);
    return {
        mydata:state.todo
    }
}

export default connect(mapStateToProps)(Todos)