import React,{Component} from 'react'
import {withRouter} from 'react-router-dom'
import 'antd-mobile/dist/antd-mobile.css'
import {TabBar} from 'antd-mobile'

class Tab extends Component{
  constructor(props) {
    super(props);
    this.state = {
      selectedTab: 'blueTab',
    };
  }
  
  render() {
    const {pathname} = this.props.location;
    // console.log(pathname);
    if(pathname.split('/').legth>2){
      console.log('null');
      return null;
    }
    return (
      <div style={{ 
        position: 'fixed', 
        width: '100%', 
        bottom: 0 }}>
        <TabBar
          unselectedTintColor="#949494"
          tintColor="#33A3F4"
          barTintColor="white"
        >
          <TabBar.Item
            title="首页"
            key="home"
            icon={
              <i className='iconfont icon-shouye1'></i>
            }
            selectedIcon={
              <i className='iconfont icon-shouye'></i>
            }
            selected={pathname === '/'}
            onPress={() => {
              this.props.history.push('/')
            }}
            // data-seed="logId"
          >
          </TabBar.Item>
          <TabBar.Item
            title="购物车"
            key="shoppingcar"
            icon={
              <i className='iconfont icon-gouwuche'></i>
            }
            selectedIcon={
              <i className='iconfont icon-gouwucheman'></i>
            }
            selected={pathname === '/Shoppingcar'}
            onPress={() => {
              this.props.history.push('/Shoppingcar')
          }}
          // data-seed="logId1"
        >
        
        </TabBar.Item>
        <TabBar.Item
        title="订单列表"
        key="Dingdan"
          icon={
            <i className='iconfont icon-dingdan1'></i>
          }
          selectedIcon={
            <i className='iconfont icon-dingdan'></i>
          }
          selected={pathname === '/Dingdan'}
          onPress={() => {
            this.props.history.push('/Dingdan')
          }}
        >
        </TabBar.Item>
        <TabBar.Item
        title="我的淘宝"
        key="my"
            icon={
              <i className='iconfont icon-wode1'></i>
            }
            selectedIcon={
              <i className='iconfont icon-wode'></i>
            }
            selected={pathname === '/My'}
            onPress={() => {
              this.props.history.push('/My')
            }}
          >
            
          </TabBar.Item>
          <TabBar.Item
            title="更多"
            key="more"
            icon={
              <i className='iconfont icon-19'></i>
            }
            selectedIcon={
              <i className='iconfont icon-gengduo'></i>
            }
            selected={pathname === '/More'}
            onPress={() => {
              this.props.history.push('/More')
            }}
          >
          </TabBar.Item>
        </TabBar>
      </div>
    );
  }
}

export default withRouter(Tab);